<template>
  <div class="status">{{ status }}</div>
  <div class="board-row">
    <Square
      id="0"
      v-bind:value="squares[0]"
      v-on:square-clicked="renderSquare"
    />
    <Square
      id="1"
      v-bind:value="squares[1]"
      v-on:square-clicked="renderSquare"
    />
    <Square
      id="2"
      v-bind:value="squares[2]"
      v-on:square-clicked="renderSquare"
    />
  </div>
  <div class="board-row">
    <Square
      id="3"
      v-bind:value="squares[3]"
      v-on:square-clicked="renderSquare"
    />
    <Square
      id="4"
      v-bind:value="squares[4]"
      v-on:square-clicked="renderSquare"
    />
    <Square
      id="5"
      v-bind:value="squares[5]"
      v-on:square-clicked="renderSquare"
    />
  </div>
  <div class="board-row">
    <Square
      id="6"
      v-bind:value="squares[6]"
      v-on:square-clicked="renderSquare"
    />
    <Square
      id="7"
      v-bind:value="squares[7]"
      v-on:square-clicked="renderSquare"
    />
    <Square
      id="8"
      v-bind:value="squares[8]"
      v-on:square-clicked="renderSquare"
    />
  </div>
</template>

<script>
import Square from "./Square";
export default {
  components: {
    Square,
  },
  props: ["status", "squares", "step", "current_player"],
  methods: {
    renderSquare(square) {
      if (calculateWinner(this.squares) || this.squares[square]) {
        return;
      }
      let sq = this.squares.slice();
      sq[square] = this.current_player;
      let player;
      if ((this.step + 1) % 2 == 0) {
        player = "X";
      } else {
        player = "O";
      }
      this.$emit("player-changed", player, sq);
    },
  },
  emits: ["player-changed"],
};
const calculateWinner = (squares) => {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  return null;
};
</script>

<style scoped>
.board-row {
  display: flex;
}
.status {
  display: flex;
}
</style>
