<template>
  <button @click="$emit('square-clicked', id)" class="square">
    {{ value }}
  </button>
</template>

<script>
const Sqaure = {
  name: "Square",
  props: ["id", "value"],
  emits: ["square-clicked"],
};

export default Sqaure;
</script>

<style scoped>
.square {
  width: 40px;
  height: 40px;
}
</style>
