<template>
  <Board
    v-bind:squares="history[step]"
    v-bind:status="status"
    v-on:player-changed="changeStatus"
    v-bind:step="step"
    v-bind:current_player="step % 2 == 0 ? 'X' : 'O'"
  />
  <ul>
    <li v-for="(state, idx) in history" v-bind:key="idx">
      <button v-if="idx == 0" @click="changeStep(idx)">Go to start</button>
      <button v-else @click="changeStep(idx)">Go to move {{ idx }}</button>
    </li>
  </ul>
</template>

<script>
import Board from "./Board";
export default {
  components: {
    Board,
  },
  data() {
    return {
      status: "Next Player: X",
      history: [Array(9).fill(null)],
      step: 0,
    };
  },
  emits: ["player-changed"],
  methods: {
    changeStep(step) {
      this.step = step;
      if (calculateWinner(this.history[step])) {
        let winner = calculateWinner(this.history[step]);
        this.status = winner == "Draw" ? winner : "The winner is: " + winner;
        return;
      }
      this.status = "Next Player: " + (step % 2 == 0 ? "X" : "O");
    },
    changeStatus(player, squares) {
      if (calculateWinner(squares)) {
        let winner = calculateWinner(squares);
        let newHistory = this.history.slice(0, this.step + 1);
        this.history = newHistory;
        this.step++;
        this.history.push(squares);
        this.status = winner == "Draw" ? winner : "The winner is: " + winner;
        return;
      }
      this.status = "Next Player: " + player;
      let newHistory = this.history.slice(0, this.step + 1);
      this.history = newHistory;
      this.step++;
      this.history.push(squares);
    },
  },
};

const calculateWinner = (squares) => {
  const lines = [
    [0, 1, 2],
    [3, 4, 5],
    [6, 7, 8],
    [0, 3, 6],
    [1, 4, 7],
    [2, 5, 8],
    [0, 4, 8],
    [2, 4, 6],
  ];
  for (let i = 0; i < lines.length; i++) {
    const [a, b, c] = lines[i];
    if (squares[a] && squares[a] === squares[b] && squares[a] === squares[c]) {
      return squares[a];
    }
  }
  let full = true;
  for (let square of squares) {
    if (!square) {
      full = false;
    }
  }
  if (full) {
    return "Draw";
  }
  return null;
};
</script>

<style>
ul {
  list-style: none;
  padding-left: 0px;
}
li {
  display: flex;
}
</style>
